            </button>
            <button class="btn" :class="activeFilter === 'fixedWing' ? 'btn-primary' : 'btn-outline-primary'" @click="setFilter('fixedWing')">
              <i class="bi bi-airplane me-1"></i>固定翼
            </button>
            <button class="btn" :class="activeFilter === 'multiRotor' ? 'btn-primary' : 'btn-outline-primary'" @click="setFilter('multiRotor')">
              <i class="bi bi-hexagon me-1"></i>多旋翼
            </button>
            <button class="btn" :class="activeFilter === 'helicopter' ? 'btn-primary' : 'btn-outline-primary'" @click="setFilter('helicopter')">
              <i class="bi bi-wind me-1"></i>直升机
            </button>
            <button class="btn" :class="activeFilter === 'fpv' ? 'btn-primary' : 'btn-outline-primary'" @click="setFilter('fpv')">
              <i class="bi bi-camera-video me-1"></i>FPV
            </button>
            <button class="btn" :class="activeFilter === 'system' ? 'btn-primary' : 'btn-outline-primary'" @click="setFilter('system')">
              <i class="bi bi-grid-3x3 me-1"></i>系统平台
            </button>
          </div>
        </div>
      </div>

      <!-- 产品展示 -->
      <div class="row g-4">
        <div class="col-12 col-md-6 col-lg-4" v-for="drone in filteredDrones" :key="drone.id">
          <div class="card h-100 product-card">
            <div class="product-image">
              <img :src="drone.image" class="card-img-top" :alt="drone.name">
              <div class="product-overlay">
                <router-link :to="'/products/platform/detail/' + drone.id" class="btn btn-light">查看详情</router-link>
              </div>
            </div>
            <div class="card-body">
              <div class="product-badge" :class="getBadgeClass(drone.type)">
                {{ getTypeName(drone.type) }}
              </div>
              <h5 class="card-title">{{ drone.name }}</h5>
              <p class="card-text">{{ drone.description }}</p>
              <div class="specs">
                <div class="specs-item" v-for="(key, index) in getMainSpecs(drone)" :key="index">
                  <span class="specs-label">{{ key }}:</span>
                  <span class="specs-value">{{ drone.specifications[key] }}</span>
                </div>
              </div>
            </div>
            <div class="card-footer bg-white border-top-0">
              <div class="d-flex gap-2">
                <router-link :to="'/products/platform/detail/' + drone.id" class="btn btn-primary flex-grow-1">产品详情</router-link>
                <button class="btn btn-outline-primary flex-grow-1">获取报价</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 产品详情模态框 -->
    <div class="modal fade" id="droneModal" tabindex="-1">
      <div class="modal-dialog modal-xl modal-dialog-scrollable">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">{{ selectedDrone ? selectedDrone.name : '' }}</h5>
            <button type="button" class="btn-close" @click="closeModal" aria-label="Close"></button>
          </div>
          <div class="modal-body" v-if="selectedDrone">
            <!-- 产品导航标签 -->
            <ul class="nav nav-tabs mb-4" id="productTab" role="tablist">
              <li class="nav-item" role="presentation">
                <button class="nav-link active" id="overview-tab" data-bs-toggle="tab" data-bs-target="#overview" type="button" role="tab">产品概述</button>
              </li>
              <li class="nav-item" role="presentation">
                <button class="nav-link" id="specs-tab" data-bs-toggle="tab" data-bs-target="#specs" type="button" role="tab">技术参数</button>
              </li>
              <li class="nav-item" role="presentation">
                <button class="nav-link" id="features-tab" data-bs-toggle="tab" data-bs-target="#features" type="button" role="tab">特点优势</button>
              </li>
              <li class="nav-item" role="presentation">
                <button class="nav-link" id="applications-tab" data-bs-toggle="tab" data-bs-target="#applications" type="button" role="tab">应用场景</button>
              </li>
              <li class="nav-item" role="presentation" v-if="selectedDrone.video">
                <button class="nav-link" id="video-tab" data-bs-toggle="tab" data-bs-target="#video" type="button" role="tab">产品视频</button>
              </li>
            </ul>

            <!-- 标签内容 -->
