                  <div class="col-lg-6">
                    <div class="product-gallery">
                      <div class="main-image mb-3">
                        <img :src="selectedDrone.image" class="img-fluid rounded" :alt="selectedDrone.name">
                      </div>
                      <div class="row g-2">
                        <div class="col-3" v-for="(img, index) in selectedDrone.gallery" :key="index">
                          <img :src="img" class="img-fluid rounded" :alt="'产品图片 ' + (index + 1)">
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="col-lg-6">
                    <div class="product-badge mb-3" :class="getBadgeClass(selectedDrone.type)">
                      {{ getTypeName(selectedDrone.type) }}
                    </div>
                    <h3 class="product-title mb-3">{{ selectedDrone.name }}</h3>
                    <p class="product-description mb-4">{{ selectedDrone.description }}</p>
                    
