                            <td>{{ spec }}</td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                  <div class="col-lg-6">
                    <img :src="selectedDrone.image" class="img-fluid rounded" :alt="selectedDrone.name">
                  </div>
                </div>
              </div>

