                    <h4 class="mb-4">产品特点</h4>
                    <ul class="feature-list">
                      <li v-for="(feature, index) in selectedDrone.features" :key="index" class="mb-3">
                        <div class="d-flex">
                          <div class="feature-icon text-accent me-3">
                            <i class="bi bi-check-circle-fill fs-4"></i>
                          </div>
                          <div>
                            <p class="mb-0">{{ feature }}</p>
                          </div>
                        </div>
                      </li>
                    </ul>
                  </div>
                  <div class="col-lg-6">
                    <div class="feature-gallery">
                      <img :src="selectedDrone.gallery[0]" class="img-fluid rounded mb-3" alt="产品特点">
                      <div class="row g-3">
                        <div class="col-6" v-for="(img, index) in selectedDrone.gallery.slice(1)" :key="index">
                          <img :src="img" class="img-fluid rounded" :alt="'产品特点 ' + (index + 1)">
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <!-- 应用场景 -->
              <div class="tab-pane fade" id="applications" role="tabpanel">
