                  </div>
                  <div class="col-12">
                    <div class="ratio ratio-16x9">
                      <iframe :src="selectedDrone.video" title="产品视频" allowfullscreen></iframe>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <div class="row w-100">
              <div class="col-lg-6 text-lg-start text-center mb-3 mb-lg-0">
