                <a href="#" class="btn btn-outline-secondary">产品参数表</a>
              </div>
              <div class="col-lg-6 text-lg-end text-center">
                <button type="button" class="btn btn-secondary me-2" @click="closeModal">关闭</button>
                <button type="button" class="btn btn-primary">获取报价</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

