  computed: {
    filteredDrones() {
      if (this.activeFilter === 'all') {
        return this.drones;
      }
      return this.drones.filter(drone => drone.type === this.activeFilter);
    }
  },
  mounted() {
    // 检查路由参数，判断是否需要显示特定产品
    const id = this.$route.params.id;
    if (id) {
      this.activeFilter = this.drones.find(d => d.id === id)?.type || 'all';
    }
  },
  methods: {
    setFilter(filter) {
      this.activeFilter = filter;
    },
    getMainSpecs(drone) {
      // 根据产品类型获取关键参数
      const specKeys = {
        fixedWing: ['最大起飞重量', '最大载重', '续航时间', '最大飞行速度'],
        multiRotor: ['最大起飞重量', '最大载重', '续航时间', '最大飞行速度'],
        helicopter: ['最大起飞重量', '最大载重', '续航时间', '最大飞行速度'],
        fpv: ['机身尺寸', '重量(不含电池)', '最大速度', '续航时间'],
        system: ['最大集群数量', '控制距离', '编队精度', '响应时间']
      };
      
      const type = drone.type || 'multiRotor';
      return specKeys[type] || Object.keys(drone.specifications).slice(0, 4);
    },
    getBadgeClass(type) {
      const classes = {
        fixedWing: 'bg-primary',
        multiRotor: 'bg-success',
        helicopter: 'bg-danger',
        fpv: 'bg-warning',
        system: 'bg-info'
      };
      return classes[type] || 'bg-secondary';
    },
    getTypeName(type) {
      const names = {
        fixedWing: '固定翼',
        multiRotor: '多旋翼',
        helicopter: '直升机',
        fpv: 'FPV',
        system: '系统平台'
      };
      return names[type] || '未分类';
    },
    getApplicationIcon(app) {
      const icons = {
        '电力巡检': 'bi bi-lightning fs-3',
